.daysContainer {
    position: relative;
    display: block;
    overflow: scroll;

    width: 50em;
    height: 90vh;

    margin: 0.5em;

    border: 2px solid;
    border-radius: 3px;
}

.mainHeaderSlot {
    display: flex;
    height: 2em;
    /* border: 2px solid;
    border-radius: 3px; */
    background-color: white;
}

.mainHeader-timeCol {
    position: sticky;
    top: 0;
    left: 0;

    width: 5em;

    background-color: rgb(71, 108, 255);
    color: white;
    font-weight: bold;
}


.mainHeader-restCols {
    width: 71em;
    height: 2em;

    /* border-radius: 3px; */

    text-align: left;
    background-color: rgb(71, 108, 255);
}

.mainHeader-restCols span {
    margin-left: 0.5em;
    display: inline-flex;
    overflow: hidden;
}

.dayBlockDiv.mainHeader {
    position: sticky;
    top: 0;
    z-index: 10;

    text-align: center;
    font-weight: bold;

    background-color: rgb(71, 108, 255);
    color: white;
}

.CurDayInview {
    position: sticky;
    display: inline-block;
    top: 3em;
    left: 0;

    z-index: 10;
    /* width: 95vw;
    max-width: 99%; */
    padding-bottom: 0.25em;
    padding-left: 10em;

    /* border: 2px solid black; */
    /* border-radius: 3px; */

    color: white;
    font-weight: bold;
    text-align: left
}